     font-size: $swal2-toast-html-container-font-size;\n      text-align: initial;\n\n      &:empty {\n        padding: 0;\n      }\n    }\n\n    .swal2-loader {\n      grid-column: 1;\n      grid-row: 1/99;\n      align-self: center;\n      width: 2em;\n      height: 2em;\n      margin: 0.25em;\n    }\n\n    .swal2-icon {\n      grid-column: 1;\n      grid-row: 1/99;\n      align-self: center;\n      width: 2em;\n      min-width: 2em;\n      height: 2em;\n      margin: 0 0.5em 0 0;\n\n      .swal2-icon-content {\n        display: flex;\n        align-items: center;\n        font-size: $swal2-toast-icon-font-size;\n        font-weight: bold;\n      }\n\n      &.swal2-success {\n        .swal2-success-ring {\n          width: 2em;\n          height: 2em;\n        }\n      }\n\n      &.swal2-error {\n        [class^='swal2-x-mark-line'] {\n          top: 0.875em;\n          width: 1.375em;\n\n          &[class$='left'] {\n            left: 0.3125em;\n          }\n\n          &[class$='right'] {\n            right: 0.3125em;\n          }\n        }\n      }\n    }\n\n    .swal2-actions {\n      justify-content: flex-start;\n      height: auto;\n      margin: 0;\n      margin-top: 0.5em;\n      padding: 0 0.5em;\n    }\n\n    .swal2-styled {\n      margin: 0.25em 0.5em;\n      padding: 0.4em 0.6em;\n      font-size: $swal2-toast-buttons-font-size;\n    }\n\n    .swal2-success {\n      border-color: $swal2-success;\n\n      [class^='swal2-success-circular-line'] {\n        // Emulate moving circular line\n        position: absolute;\n        width: 1.6em;\n        height: 3em;\n        transform: rotate(45deg);\n        border-radius: 50%;\n\n        &[class$='left'] {\n          top: -0.8em;\n          left: -0.5em;\n          transform: rotate(-45deg);\n          transform-origin: 2em 2em;\n          border-radius: 4em 0 0 4em;\n        }\n\n        &[class$='right'] {\n          top: -0.25em;\n          left: 0.9375em;\n          transform-origin: 0 1.5em;\n          border-radius: 0 4em 4em 0;\n        }\n      }\n\n      .swal2-success-ring {\n        width: 2em;\n        height: 2em;\n      }\n\n      .swal2-success-fix {\n        top: 0;\n        left: 0.4375em;\n        width: 0.4375em;\n        height: 2.6875em;\n      }\n\n      [class^='swal2-success-line'] {\n        height: 0.3125em;\n\n        &[class$='tip'] {\n          top: 1.125em;\n          left: 0.1875em;\n          width: 0.75em;\n        }\n\n        &[class$='long'] {\n          top: 0.9375em;\n          right: 0.1875em;\n          width: 1.375em;\n        }\n      }\n\n      &.swal2-icon-show {\n        @if $swal2-icon-animations {\n          .swal2-success-line-tip {\n            animation: swal2-toast-animate-success-line-tip 0.75s;\n          }\n\n          .swal2-success-line-long {\n            animation: swal2-toast-animate-success-line-long 0.75s;\n          }\n        }\n      }\n    }\n\n    &.swal2-show {\n      animation: $swal2-toast-show-animation;\n    }\n\n    &.swal2-hide {\n      animation: $swal2-toast-hide-animation;\n    }\n  }\n}\n","$swal2-white: #fff !default;\n$swal2-black: #000 !default;\n$swal2-outline-color: rgba(100, 150, 200, 0.5) !default;\n\n// CONTAINER\n$swal2-container-padding: 0.625em !default;\n\n// POPUP\n$swal2-width: 32em !default;\n$swal2-padding: 0 0 1.25em !default;\n$swal2-border: none !default;\n$swal2-color: lighten($swal2-black, 33) !default;\n$swal2-border-radius: 5px !default;\n$swal2-box-shadow: #d9d9d9 !default;\n\n// ANIMATIONS\n$swal2-show-animation: swal2-show 0.3s !default;\n$swal2-hide-animation: swal2-hide 0.15s forwards !default;\n\n// BACKGROUND\n$swal2-background: $swal2-white !default;\n\n// TYPOGRAPHY\n$swal2-font: inherit !default;\n$swal2-font-size: 1rem !default;\n\n// BACKDROP\n$swal2-backdrop: rgba($swal2-black, 0.4) !default;\n$swal2-backdrop-transition: background-color 0.1s !default;\n\n// ICONS\n$swal2-icon-size: 5em !default;\n$swal2-icon-animations: true !default;\n$swal2-icon-margin: 2